	display: flex;
}

.photo {
	width: 75%;
	border-color: #343a40;
	border: 2px;
	border-style: solid;
}

.photoColumn {
	text-align: center;
	align-self: center;
}

.textColumn {
	color: #f5f5f5;
	align-self: center;
}